body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;color:#000;text-align:center;word-wrap:break-word;scroll-behavior:smooth}h1,h2,h3,h4,h5,h6{margin:1vh 1vw 2vh 1vw}p{text-align:center;font-size:1.2rem;margin-bottom:2vh}h1,h2,h3,h4,h5,h6,p,strong{color:inherit}.material-icons{transition:transform 0.5s}.material-icons:hover{transform:scale(1.2)}.material-icons:active{transform:scale(2)}#block1{position:absolute;top:9vh;right:4vw;width:16vmin;height:16vmin;background:#ee1044;opacity:0.2}#block2{position:absolute;top:6vh;right:8vw;width:15vmin;height:15vmin;background:#acd43f;opacity:0.1}header{background:white;border-bottom:1px solid rgba(127,127,127,0.5);display:flex;flex-direction:row;height:minmax(42px, auto);align-content:space-between;z-index:10;position:-webkit-sticky;position:sticky;top:0}.home-portal{font-size:1.2rem;font-weight:400;letter-spacing:0.05rem;margin:auto calc(1rem + 2vw)}.home-portal a{text-decoration:none}.home-portal span{transition:color 0.5s ease-in-out, font-size 1s ease-in-out, font-weight 0.5s ease-in-out}.home-portal:hover span{font-size:1.25rem;font-weight:600}.home-portal:hover span:nth-child(1){color:#d43f62}.home-portal:hover span:nth-child(2){color:#acd43f}.home-portal:hover span:nth-child(3){color:#3fd48f}.home-portal:hover span:nth-child(4){color:#4b48e0}nav{padding:0 calc(1rem + 5vw);text-align:right;width:100%}nav a{background:white;display:inline-block;position:relative;font-weight:600;text-decoration:none;padding:11px 2vw 9px 2vw;transition:background 0.5s, color 0.5s}nav a:hover{background:#d43f62;color:white;transition:background 0.5s;cursor:pointer;border-bottom:2px solid white}main{position:relative;text-align:center}main .spacer{height:10vh}main .intro{display:flex;flex-direction:column;justify-content:center;position:relative;width:50%;height:100vh;margin:0 auto;font-size:120%;border-bottom:1px solid #aaa}main .about{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:center}main .about h1{color:white;margin:4vh 2vw;font-size:160%}main .about .about-me{color:white;padding:10vh 10vw;background-color:#d43f62}main .about .about-me .material-icons{color:white;font-size:200%;padding:2vh 1vw 1vh 1vw;cursor:default}main #about-skills{padding:5vh 20vw 5vh 10vw}main .projects{display:flex;flex-direction:column;align-items:center;margin:0 auto}main .social{position:relative;display:block;padding:2vh 2vw}main .social a{margin:1vh 1vw}main .social a img{transition:transform 0.5s}main .social a img:hover{transform:translate3d(0px, -10px, 0px);transition:transform 0.5s}main .social a img:active{transform:scale(1.2);transition:transform 0.5s}main button{font-size:1.2rem;font-weight:bold;color:#ee1044;margin:4vh;text-transform:uppercase;padding:8px 16px;background:white;border:3px solid #ee1044;border-radius:6px;transition:transform 0.5s}main button:hover{transform:scale(1.05);cursor:pointer}main button:active{transform:scale(1.2)}#footer-line{display:block;height:5px;width:100%;background:#d43f62}footer{margin:10vh auto 5vh auto}footer #imprint{text-decoration:underline;cursor:pointer}footer div{padding-top:4vh;font-size:0.75rem}.tag{display:inline-block;transform:scale(0.75);margin:0}#projects-banner{padding:2vh 0 2vh 0;width:100%;background:#d43f62;border-top:5px solid #3fd48f;margin-bottom:10vh}#projects-banner h1{color:white}.project-card-grid{display:grid;margin:2vh;width:70vw;border:1px solid #ccc;border-radius:4px;font-size:80%;transition:transform 0.5s}.project-card-grid:hover{transform:scale(1.05);transition:transform 0.5s}.project-card-grid:active{transform:scale(1.06);transition:transform 0.1s}.project-card-grid .left{min-width:200px;border-right:1px solid #e4e4e4}.project-card-grid .left img{width:100%;height:auto;object-fit:contain}.project-card-grid .main .mainDesc{padding:2vh 1vw}.project-card-grid .main .mainDesc h3{text-align:left;margin:1vh 0}.project-card-grid .main .mainDesc p{text-align:justify;margin-bottom:0.5vh;font-size:1rem}.project-card-grid .main .mainInfo{padding:2vh 1vw;text-align:left}.project-card-grid .main .bottom{padding:2vh 1vw;display:grid;grid-template-columns:1fr auto}.project-card-grid .main .bottom .links{text-align:left}.project-card-grid .main .bottom .meta{font-style:italic;opacity:0.7;padding-top:5px}.skill-card{display:grid;grid-template:1fr / 1fr 4fr;font-size:120%;padding:5vh 0}.skill-card *{text-align:left;margin:0}.skill-card h2{padding:1vh 0}.skill-card i{font-size:300%;padding:4vmin}.skill-card:nth-child(3n+1) h2,.skill-card:nth-child(3n+1) i{color:#d43f62}.skill-card:nth-child(3n+2) h2,.skill-card:nth-child(3n+2) i{color:#acd43f}.skill-card:nth-child(3n) h2,.skill-card:nth-child(3n) i{color:#3fd48f}.tag-item :hover,.tag-item.alternate:hover,.tag-item.primary,.tag-item.tertiary:hover{color:#d43f62;border:2px solid #d43f62}.tag-item ,.tag-item.primary:hover,.tag-item.secondary{color:#acd43f;border:2px solid #acd43f}.tag-item.alternate,.tag-item.secondary:hover,.tag-item.tertiary{color:#3fd48f;border:2px solid #3fd48f}.tag-item{display:inline-block;font-size:0.6rem;font-weight:bold;margin:0.5vmin;padding:4px 8px;background:white;border-radius:6px;transition:color 0.5s, border 0.5s}.tag-item:hover{cursor:default}@media only screen and (max-width: 8640px){.grid{grid-template-rows:repeat(1, 1fr);grid-template-columns:repeat(3, 1fr)}.grid span:nth-child(2){border-width:0px 1px}.project-card-grid{grid-template-rows:1fr;grid-template-columns:1fr 5fr}.project-card-grid .main{margin:1vh 1vw}}@media only screen and (max-width: 1024px){h1,h2{font-size:1.25em}p{font-size:0.9em}.about-me{font-size:1.1rem}.intro{width:75%}button{transform:scale(0.9)}}@media only screen and (max-width: 768px){.home-portal{display:none}nav{text-align:center}h1,h2{font-size:1em}p{font-size:0.8em}.about-me{font-size:1.1rem}.grid{grid-template-rows:repeat(3, 1fr);grid-template-columns:repeat(1, 1fr)}.grid span:nth-child(2){border-width:1px 0px}.project-card-grid{grid-template-rows:auto 1fr;grid-template-columns:1fr}.project-card-grid .left{border-bottom:1px solid #e4e4e4}.project-card-grid .main{margin:2vh 3vw}button{transform:scale(0.8)}}

